.tab-set {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0;
  padding: 0;
}

.tab-set a {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: center;
  font-size: 0.8rem;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.425);
  padding: 0.5rem 0;
  margin: 0 1.2rem;
  border-bottom: 4px solid transparent;
  cursor: pointer;
}

.tab-set a svg {
  display: block;
  margin: 0 auto;
  font-size: 1.2rem;
}

.tab-set a.active {
  border-bottom: 4px solid #2c2c2c;
  color: #2c2c2c;
}

.tab-set a:hover {
  border-bottom: 4px solid rgba(44, 70, 70, 0.233);
}

.tab-set a.active:hover {
  border-bottom: 4px solid #2c4646;
}
