@font-face {
    font-family: "nunito";
    src: url(../../../../assets/fonts/Nunito-VariableFont_wght.ttf);
}


header {
	width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 2px 5px rgba(0,0,0,0.3);
    #logo{
        display: flex;
        align-items: center;
        text-decoration: none;
        color: white;
        font-family: monospace;
        img {
            margin: 0 1rem 0 0.6rem;
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
        }
    }
    #logged-header {
        display: flex;
    }
}
@media (max-width: 750px) {
    #logo p {display: none;}
    #logged-header {
        display: flex;
        justify-content: space-between;
    }
}
@media (max-width: 600px) {
    #logo * {display: none;}
}
